* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', sans-serif;
  background-color: rgb(240, 240, 240);
}

ul {
  list-style: none;
}

.title {
  font-size: 60px;
  text-align: center;
  margin: 30px 50px;
  color: rgb(1, 203, 190);
}

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 700px;
  margin: 50px auto;
}

.card {
  background-color: #fff;
  border-radius: 10px;
  width: 200px;
  height: 250px;
  margin: 20px 10px;
  padding: 15px 20px;
  text-align: center;
  box-shadow: 0 5px 20px 2px rgba(0, 0, 0, 0.084);
  cursor: pointer;
  -webkit-transition: .3s all;
  transition: .3s all;
}

.card:hover {
  box-shadow: 0 15px 25px 5px rgba(0, 0, 0, 0.084);
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}

.name {
  color: rgb(108, 218, 207);
  text-transform: capitalize;
  margin-top: 20px;
}
.type {
  margin: 20px auto;
  display: flex;
  justify-content: center;
}
.types {
  background-color: rgb(108, 218, 207);
  margin: 0 5px;
  padding: 7px 15px;
  font-size: 15px;
  line-height: 15px;
  border-radius: 6px;
  box-shadow: 0 5px 15px 0 rgba(12, 189, 171, 0.5);
  color: #fff;
  font-weight: 400;
}
